/**
 * import Vue
 * vue
 */
import App from './vue/App.js';

/**
 * import node_modules
 */
import AOS from 'aos';
import 'desvg';
import objectFitImages from 'object-fit-images';
import picturefill from 'picturefill';
import SmoothScroll from 'smooth-scroll';

/**
 * import modules
 */
import Sample from './modules/sample';
import Common from './modules/common';

/**
 * Main
 * @constructor
 */
class Main {
  constructor() {
    this.setup();
  }
  /**
   * 
   */
  setup() {
    document.addEventListener('DOMContentLoaded', () => {
      this.created();
    })
    window.addEventListener('load', () => {
      this.mounted();
    });
  }
  /**
   * 
   */
  created() {
    picturefill();

    const sample = new Sample();
    sample.init();

    const common = new Common();
    common.init();
  }
  /**
   * 
   */
  mounted() {

    this.init_aos();

    this.init_deSVG('.js-desvg');

    this.init_objectFitImages('.object-fit');

    this.init_smoothScroll();
  }

  /**
   * /AOS
   * 
   * https://michalsnik.github.io/aos/
   */
  init_aos() {
    /**
     * 
     * @type {object}
     */
    const option_aos = {
      offset: 400,
      duration: 750,
      easing: 'ease-out-back',
      delay: 200,
      anchorPlacement: 'top-bottom',
      once: true,
    }
    AOS.init(option_aos);
  }

  /**
   * /deSVG
   * svgsvg
   * https://github.com/benhowdle89/deSVG
   * @param {Element} target 
   */
  init_deSVG(target) {
    deSVG(target, true)
  }

  /**
   * /object-fit-images
   * css object-fit 
   * https://github.com/fregante/object-fit-images
   * @param {Element} target 
   */
  init_objectFitImages(target) {
    objectFitImages(target);
  }

  /**
   * /picturefill
   * html picture
   * https://scottjehl.github.io/picturefill/
   */
  init_picturefill() {
    picturefill();
  }

  /**
   * /smooth-scroll
   * 
   * https://github.com/cferdinandi/smooth-scroll
   */
  init_smoothScroll() {
    /**
     * 
     * @type {Element}
     */
    const targetElement = 'a[href*="#"]';

    /**
     * 
     * @type {object}
     */
    const option = {
      speed: 1000,
      easing: 'easeInOutCubic',
      speedAsDuration: true,
      //header: '.l-header' 
    }

    new SmoothScroll(targetElement, option);
  }
}

export default new Main();
